.header
    position: fixed
    width: 100%
    left: 0
    top: 0
    z-index: 500
    transition: background-color 0.3s ease 0s
    &.active
        background-color: rgba(255, 255, 255, 0.8)
        box-shadow: $box-shadow
        @media (any-hover: hover)
            &:hover
                background-color: $white-color
                transition: background-color 0.3s ease 0s

    &__container

    &__body
        display: flex
        align-items: center
        justify-content: space-between
        min-height: 70px

    &__logo
        font-size: 25px
        color: $dark-color
        font-weight: 900
        flex: 0 0 180px
        margin-right: 20px
        &:before
            display: inline
            color: $green-color
            padding-right: 5px

    &__main
        display: flex
        align-items: center

    &__navbar
        @media (max-width: $md2)
            position: fixed
            top: 0
            left: 0
            transform: translateX(-100%)
            width: 50%
            height: 100%
            z-index: 1000
            display: flex
            align-items: center
            justify-content: center
            overflow-y: auto
            overflow-x: hidden
            background-color: $white-color
            padding: 40px 20px
            transition: transform 0.5s ease 0s, box-shadow 0.8s ease 0s
            &.active
                transform: translateX(0)
                box-shadow: 0 0 0 100vw rgba(0, 0, 0, 0.8)
                transition: transform 0.5s ease 0s, box-shadow 1.2s ease 0s
        @media (max-width: $md3)
            width: 60%
        @media (max-width: $md4)
            width: 70%
            flex-direction: column

    &__menu
        display: flex
        align-items: center
        flex-wrap: wrap
        @media (max-width: $md2)
            flex-wrap: nowrap
            flex-direction: column
        @media (max-width: $md4)
            margin-bottom: 40px

    &__item
        margin: 10px 0
        &:not(:last-child)
            margin-right: 25px
        @media (max-width: $md2)
            margin: 0
            &:not(:last-child)
                margin: 0 0 40px 0
        @media (max-width: $md4)
            margin: 0
            &:not(:last-child)
                margin: 0 0 25px 0

    &__link
        font-size: 18px
        font-weight: 700
        transition: color 0.3s ease 0s
        @media (any-hover: hover)
            &:hover
                color: $green-color
        @media (max-width: $md2)
            font-size: 42px
        @media (max-width: $md3)
            font-size: 36px
        @media (max-width: $md4)
            font-size: 26px

    &__actions
        display: flex
        align-items: center
        flex-wrap: nowrap

    &__user,
    &__search,
    .cart-header__btn
        transition: color 0.3s ease 0s
        @media (any-hover: hover)
            &:hover
                color: $green-color

    &__user
        font-size: 20px
        margin-right: 20px
        @media (max-width: $md4)
            margin-right: 40px

    &__cart
        margin-right: 20px
        @media (max-width: $md4)
            margin-right: 25px

    &__search
        font-size: 20px
        @media (max-width: $md2)
            margin-right: 20px
        @media (max-width: $md4)
            margin-right: 0

    &__icons
        display: none
        @media (max-width: $md4)
            display: flex
            align-items: center

    &__burger
        display: none
        @media (max-width: $md2)
            display: block
            position: relative
            width: 30px
            height: 16px
            cursor: pointer
            z-index: 1001
            transition: all 0.3s ease 0s
            @media (any-hover: hover)
                &:hover
                    span
                        background-color: $green-color

            span
                position: absolute
                height: 2px
                width: 100%
                background-color: $dark-color
                transition: all 0.3s ease 0s
                left: 0
                &:nth-child(1)
                    top: 0
                &:nth-child(2)
                    top: 7px
                &:nth-child(3)
                    bottom: 0

        &.active
            span
                transition: all 0.3s ease 0s
                background-color: $white-color
                &:nth-child(1)
                    top: 7px
                    transform: rotate(-45deg)
                &:nth-child(2)
                    transform: scale(0)
                &:nth-child(3)
                    bottom: 7px
                    transform: rotate(45deg)
            @media (any-hover: hover)
                &:hover
                    span
                        transition: all 0.3s ease 0s
                        background-color: $green-color

.cart-header

    &__btn
        font-size: 23px

    &__content
