.reviews

    &__container

    &__title
        margin-bottom: 25px
        text-align: center

    &__slider

    &__wrapper

    &__item
        display: flex
        align-items: center
        flex-direction: column
        min-height: 285px
        @media (max-width: $md1)
            min-height: 308px
        @media (max-width: $md2)
            min-height: 280px
        @media (max-width: 730px)
            min-height: 380px
        @media (max-width: 520px)
            min-height: 280px

.item-reviews

    &__text
        flex: 1 1 auto
        padding: 20px
        font-style: italic
        background: $light-color
        border-radius: 5px
        line-height: 150%
        position: relative
        z-index: 0
        margin-bottom: 30px
        margin-top: auto
        // min-height: 210px
        &:before
            content: ''
            position: absolute
            bottom: -13px
            left: 7px
            height: 30px
            width: 30px
            background-color: $light-color
            transform: rotate(45deg)

    &__user
        display: flex
        align-items: center

    &__image
        height: 70px
        flex: 0 0 70px
        margin-right: 10px
        img
            border-radius: 100%

    &__info

    &__name
        font-size: 20px
        font-weight: 500

    &__date
        color: $green-color
