.modal
    position: fixed
    top: 0
    left: 0
    height: 100%
    width: 100%
    background-color: rgba(0, 0, 0, 0.8)
    display: flex
    align-items: center
    justify-content: center
    z-index: 1000
    transform: translateY(-100%)
    transition: transform 0.5s ease 0s
    &.active
        transform: translateY(0%)

    &__body

    &__content,
    &__product,
    &__search
        display: none
        &.active
            display: block

    &__search
        padding: 50px 10px

    &__content
        width: 600px
        height: 500px
        border-radius: 20px
        padding: 20px
        background-color: $white-color
        @media (max-width: $md3)
            width: 90vw
            height: 60vh
        @media (max-width: $md4)
            height: 80vh

    &__product
        width: 600px
        height: 500px
        border-radius: 20px
        padding: 20px
        background-color: $white-color
        @media (max-width: $md3)
            width: 90vw
            height: 55vh
        @media (max-width: $md4)
            height: 80vh

    &__login
        width: 300px
        height: 300px
        border-radius: 20px
        padding: 20px
        background-color: $white-color
        display: none
        &.active
            display: flex
        @media (max-width: 350px)
            width: 260px

    &__close
        position: absolute
        top: 30px
        right: 30px
        cursor: pointer
        color: $white-color
        font-size: 40px
        transition: color 0.3s ease 0s
        @media (any-hover: hover)
            &:hover
                color: $green-color
        @media (max-width: $md4)
            font-size: 30px
            top: 20px
            right: 25px
        @media (max-width: 350px)
            font-size: 26px

.search-form
    width: 600px
    @media (max-width: $md3)
        width: 500px
    @media (max-width: $md3)
        width: 280px

    &__body
        display: flex
        align-items: center
        padding: 5px 0
        border-bottom: 2px solid $white-color

    &__input
        width: 100%
        font-size: 18px
        color: $white-color
        text-transform: none
        background: none
        padding-right: 20px
        @media (max-width: $md4)
            font-size: 16px

    &__btn
        color: $white-color
        font-size: 20px
        transition: color 0.3s ease 0s
        @media (any-hover: hover)
            &:hover
                color: $green-color

.content-modal

    &__wrapper
        width: 100%
        height: 100%
        overflow-y: auto
        overflow-x: hidden

    &__image
        height: 350px
        img
            border-top-left-radius: 20px
            border-top-right-radius: 20px

    &__body
        padding: 10px
        @media (max-width: $md4)
            padding: 5px

    &__top
        margin: 10px 0
        display: flex
        align-items: center
        justify-content: space-between

    &__title
        color: $green-color
        font-size: 24px
        font-weight: 700
        padding-right: 10px

    &__descr
        line-height: 120%

    &__addition
        font-weight: 700

.product-modal

    &__wrapper
        width: 100%
        height: 100%
        overflow-y: auto
        overflow-x: hidden

    &__body
        display: grid
        grid-template-columns: repeat(2, 1fr)
        column-gap: 30px
        padding-right: 5px
        margin-bottom: 15px
        @media (max-width: 600px)
            grid-template-columns: 1fr
            padding: 0

    &__image
        @media (max-width: 600px)
            margin-bottom: 20px
            border-top-left-radius: 20px
            border-top-right-radius: 20px
            img
                border-top-left-radius: 20px
                border-top-right-radius: 20px

    &__content

    &__title
        color: $green-color
        font-size: 24px
        font-weight: 700
        margin-bottom: 15px

    &__price
        font-size: 20px
        font-weight: 700
        margin-bottom: 10px

    &__stars
        display: flex
        align-items: center
        margin-bottom: 30px
        color: gold
        @media (max-width: 600px)
            margin-bottom: 10px

    &__spec
        margin-bottom: 25px
        p
            color: $green-color
            &:not(:last-child)
                margin-bottom: 5px
            span
                padding-right: 5px
                color: $dark-color
                font-weight: 400

    &__btn
        height: 45px
        padding: 0 25px

    &__descr
        line-height: 120%
        padding-right: 5px
        p
            &:not(:last-child)
                margin-bottom: 5px

.login-modal
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center

    &__title
        color: $green-color
        font-size: 24px
        font-weight: 700
        margin-bottom: 25px

    &__content
        width: 100%
        padding: 0 20px

    &__item
        display: block
        width: 100%
        margin-bottom: 20px

    &__input
        width: 100%
        border-bottom: $border
        font-size: 14px
        padding: 5px

    &__checkbox

    &__tag
        padding-left: 25px
        font-size: 14px

    &__btn
        height: 40px
        padding: 0 40px
