.home
    height: 100vh

    &__slider
        height: 100%
        width: 100%
        position: relative

.slider-home

    &__body
        height: 100%
        width: 100%

    &__wrapper

    &__slide

        &_1
            background: url('../img/home/01.jpg') center/cover no-repeat
            .slider-home__container
                justify-content: flex-end
            @media (max-width: $md2)
                background: url('../img/home/04.jpg') center/cover no-repeat
                .slider-home__container
                    justify-content: center

        &_2
            background: url('../img/home/02.jpg') center/cover no-repeat
            @media (max-width: $md2)
                background: url('../img/home/05.jpg') center/cover no-repeat

        &_3
            background: url('../img/home/03.jpg') center/cover no-repeat
            .slider-home__container
                justify-content: center
            @media (max-width: $md2)
                background: url('../img/home/06.jpg') center/cover no-repeat

    &__container
        height: 100%
        width: 100%
        display: flex
        align-items: center
        @media (max-width: $md2)
            justify-content: center

    &__content
        padding: 0 50px 0 100px
        max-width: 600px
        @media (max-width: $md2)
            padding: 0 30px
            max-width: 500px
            text-align: center

    &__overhead
        font-size: 40px
        font-weight: 500
        margin-bottom: 5px
        @media (max-width: $md3)
            font-size: 36px
        @media (max-width: $md4)
            font-size: 26px

    &__title
        font-size: 60px
        font-weight: 700
        color: $green-color
        text-transform: uppercase
        margin-bottom: 15px
        @media (max-width: $md3)
            font-size: 52px
        @media (max-width: $md4)
            font-size: 38px

    &__descr
        line-height: 150%
        margin-bottom: 20px
        @media (max-width: $md4)
            line-height: 130%
            font-size: 16px
            margin-bottom: 15px

    &__btn
        height: 50px
        padding: 0 25px
        font-weight: 700
        @media (max-width: $md4)
            height: 45px
            padding: 0 20px

    &__arrows
        padding: 0 30px

    &__arrow
