* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  color: inherit;
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  line-height: 1;
  font-family: "Poppins", sans-serif;
  color: #10221b;
  font-weight: 500;
  font-style: normal;
}

input {
  outline: none;
}

input,
button,
textarea {
  font-size: inherit;
  font-family: "Poppins", sans-serif;
}

button {
  cursor: pointer;
  background-color: transparent;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
}
.main__adventures, .main__about, .main__products, .main__packages, .main__reviews, .main__subscribe, .main__blog {
  padding: 40px 0;
}
@media (max-width: 480px) {
  .main__adventures, .main__about, .main__products, .main__packages, .main__reviews, .main__subscribe, .main__blog {
    padding: 25px 0;
  }
}

.container {
  max-width: 1240px;
  padding: 0 15px;
  margin: 0 auto;
}
@media (max-width: 768px) {
  .container {
    padding: 0 10px;
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #10221b;
  border: 2px solid #10221b;
  font-size: 18px;
  transition: all 0.3s ease 0s;
  font-weight: 700;
}
@media (any-hover: hover) {
  .btn:hover {
    background-color: #10221b;
    color: #fff;
  }
}

.title {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 36px;
}
@media (max-width: 480px) {
  .title {
    font-size: 28px;
  }
}

.slider-arrows {
  width: 100%;
  display: flex;
  justify-content: space-between;
  position: absolute;
  top: 50%;
  left: 0;
  z-index: 10;
  transform: translateY(-50%);
}
@media (max-width: 768px) {
  .slider-arrows {
    display: none;
  }
}

.slider-arrow {
  font-size: 30px;
  transition: transform 0.3s ease 0s;
}
@media (any-hover: hover) {
  .slider-arrow:hover {
    transform: scale(1.2);
  }
}

.checkbox {
  appearance: none;
}
.checkbox:checked + .checkbox-tag::after {
  transform: scale(1);
}
.checkbox input::-webkit-outer-spin-button,
.checkbox input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.checkbox-tag {
  position: relative;
  display: inline-block;
}
.checkbox-tag::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  border: 2px solid #219150;
}
.checkbox-tag::after {
  transition: all 0.3 ease 0s;
  position: absolute;
  content: "";
  top: 3px;
  left: 3px;
  width: 9px;
  height: 9px;
  background-color: #219150;
  transform: scale(0);
}

