.header {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 500;
  transition: background-color 0.3s ease 0s;
}
.header.active {
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
}
@media (any-hover: hover) {
  .header.active:hover {
    background-color: #fff;
    transition: background-color 0.3s ease 0s;
  }
}
.header__body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 70px;
}
.header__logo {
  font-size: 25px;
  color: #10221b;
  font-weight: 900;
  flex: 0 0 180px;
  margin-right: 20px;
}
.header__logo:before {
  display: inline;
  color: #219150;
  padding-right: 5px;
}
.header__main {
  display: flex;
  align-items: center;
}
@media (max-width: 992px) {
  .header__navbar {
    position: fixed;
    top: 0;
    left: 0;
    transform: translateX(-100%);
    width: 50%;
    height: 100%;
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow-y: auto;
    overflow-x: hidden;
    background-color: #fff;
    padding: 40px 20px;
    transition: transform 0.5s ease 0s, box-shadow 0.8s ease 0s;
  }
  .header__navbar.active {
    transform: translateX(0);
    box-shadow: 0 0 0 100vw rgba(0, 0, 0, 0.8);
    transition: transform 0.5s ease 0s, box-shadow 1.2s ease 0s;
  }
}
@media (max-width: 768px) {
  .header__navbar {
    width: 60%;
  }
}
@media (max-width: 480px) {
  .header__navbar {
    width: 70%;
