}
.item-adventures__descr {
  flex: 1 1 auto;
  line-height: 140%;
  margin-bottom: 15px;
}
.item-adventures__btn {
  flex: 0 0 40px;
  width: 150px;
  padding: 5px;
}
.item-adventures__hidden {
  display: none;
}

.about {
