  display: flex;
  align-items: center;
  flex-direction: column;
  border: 2px solid #10221b;
}
@media (any-hover: hover) {
  .products__item:hover .item-products__icons {
    transform: translateY(0);
    transition: transform 0.5s ease 0s;
  }
}
.products__arrows {
  padding: 0 30px;
}

.item-products {
  min-height: 426px;
