  width: 100%;
  overflow: hidden;
}
.item-products__icons {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  position: absolute;
  bottom: 20px;
  left: 0;
  z-index: 10;
}
@media (any-hover: hover) {
  .item-products__icons {
    transform: translateY(70px);
    transition: transform 0.5s ease 0s;
  }
