.item-products__btn {
  height: 45px;
  flex: 0 0 45px;
  font-size: 25px;
  background-color: #10221b;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s ease 0s;
}
.item-products__btn:not(:last-child) {
  margin-right: 2px;
}
@media (any-hover: hover) {
  .item-products__btn:hover {
    background-color: #219150;
  }
}
.item-products__content {
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 20px;
}
.item-products__title {
  flex: 1 1 auto;
  font-size: 20px;
  line-height: 110%;
  margin-bottom: 10px;
}
.item-products__price {
