  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 20px;
}
@media (max-width: 768px) {
  .packages__wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 20px;
  }
}
.packages__item {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 20px;
  background: #fff;
  border: 2px solid #10221b;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease 0s;
}
@media (any-hover: hover) {
  .packages__item:hover {
    background: #10221b;
  }
  .packages__item:hover .item-packages__content {
    color: #fff;
  }
  .packages__item:hover .item-packages__btn {
    border-color: #fff;
    color: #fff;
  }
  .packages__item:hover .item-packages__btn:hover {
    background-color: #fff;
    color: #10221b;
  }
}

.item-packages__image {
  height: 250px;
  width: 100%;
