  flex-direction: column;
  align-items: center;
}
.item-packages__title {
  color: #219150;
  font-size: 20px;
  margin-bottom: 10px;
  transition: all 0.3s ease 0s;
}
.item-packages__descr {
  flex: 1 1 auto;
  line-height: 140%;
  margin-bottom: 10px;
  transition: all 0.3s ease 0s;
}
.item-packages__price {
  font-size: 20px;
  color: #219150;
  margin-bottom: 10px;
  transition: all 0.3s ease 0s;
}
.item-packages__btn {
