  .reviews__item {
    min-height: 280px;
  }
}

.item-reviews__text {
  flex: 1 1 auto;
  padding: 20px;
  font-style: italic;
  background: #eee;
  border-radius: 5px;
  line-height: 150%;
  position: relative;
  z-index: 0;
  margin-bottom: 30px;
  margin-top: auto;
}
.item-reviews__text:before {
  content: "";
  position: absolute;
  bottom: -13px;
  left: 7px;
  height: 30px;
  width: 30px;
  background-color: #eee;
  transform: rotate(45deg);
}
.item-reviews__user {
