.footer {
  padding: 50px 0;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../img/footer/footer.jpg") center/cover fixed no-repeat;
}
@media (max-width: 480px) {
  .footer {
    padding: 25px 10px;
  }
}
.footer__content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  justify-content: center;
  margin-bottom: 40px;
}
@media (max-width: 480px) {
  .footer__content {
    margin-bottom: 30px;
  }
}
.footer__column {
  margin-bottom: 10px;
}
.footer__credit {
  text-align: center;
  line-height: 140%;
  font-size: 20px;
  color: #fff;
}
.footer__credit span {
  color: #219150;
}

.column-footer__title {
  font-size: 22px;
  margin-bottom: 25px;
  color: #fff;
}
@media (max-width: 480px) {
  .column-footer__title {
    margin-bottom: 20px;
  }
}
.column-footer__link {
  display: block;
  font-size: 14px;
  color: #fff;
  margin-bottom: 20px;
  transition: color 0.3s ease 0s;
}
.column-footer__link:last-child {
  margin-bottom: 0;
}
.column-footer__link:before {
  display: inline-block;
  color: #219150;
  padding-right: 5px;
  transition: padding-right 0.3s ease 0s;
}
