.subscribe
    background: $light-color

    &__container
        max-width: 700px
        margin: 0 auto
        padding: 0 15px
        @media (max-width: $md4)
            padding: 0 10px

    &__title
        text-align: center
        margin-bottom: 25px

    &__text
        line-height: 140%
        margin-bottom: 20px

    &__form
        display: flex
        align-items: center
        height: 50px
        background-color: $white-color
        border-radius: 20px
        border: $border
        padding: 5px

    &__input
        width: 100%
        padding: 0 10px

    &__button
        height: 100%
        padding: 0 20px
        border-radius: 20px
